export default {
	data() {
		return {
			list: [{id:1,money: 20},{id:2,money: 40},{id:3,money: 20},{id:4,money: 20},{id:5,money: 30},{id:6,money: 25},{id:7,money: 30},{id:8,money: 25},{id:9,money: 40}],
			selectedList: [],
			isAllChecked: false,
			show: false,
			editValue: '',
			clickMoney: ''
		}
	},
	// created() {
	// 	console.log('加载')
	// },
	// onLoad() {
	// 	console.log('onload页面')
	// },
	methods: {
		editMoney(item) {
			console.log(item)
			this.show = true
			this.clickMoney = item.money
		},
		onChange(v) {
			this.editValue = v.detail
		},
		onClose() {
			this.show = false
		},
		confirm1() {
			this.show = false
		},
		handleCheck(item) {
			this.Item = item
			let selectedIndex = this.selectedList.indexOf(item.id)
			 if(selectedIndex >= 0) {
				this.selectedList.splice(selectedIndex, 1)
			}else{
				this.selectedList.push(item.id)
			}
			if(this.selectedList.length == this.list.length) {
				this.isAllChecked = true
			}else{
				this.isAllChecked = false
			}
		},
		// 全选
		allCheck() {
			this.isAllChecked = !this.isAllChecked
			if(!this.isAllChecked) {
				this.selectedList = []
			}else{
				this.selectedList = []
				this.list.forEach(function(item){
					this.selectedList.push(item.id)
				},this)
			}
		},
		// 添加会员
		addMember() {
			uni.navigateTo({
				url: '/pages/groupOwnerManage/searchMember/searchMember'
			})
		}
	}
}
